import './Header.css'
import LottieAnim from '../LottieAnim/'
import scroll from '../../assets/lottie/scroll.json'
const Header =()=>(
  <main className="Header">
    <header>
      <h1>Keeping your vote safe and secure – an inside story from the 2020 election</h1>
    </header>
    <section>
      <h2>Intro</h2>
      <p>While the eyes of the world were on the 2020 U.S. election, behind the scenes, public- and private-sector organizations of all sizes mobilized teams of engineers, advisors, and cybersecurity professionals.</p>
      <p>States, like Idaho, with diverse county-level needs adjusted to the changes brought about by the pandemic.</p>
      <p>Groups like the Brennan Center for Justice and federal government agencies like CISA devised trainings that drew upon their experience.</p>
      <p>Technology vendors, like BPro, supported hundreds of county election officials nationwide through this intense period.</p>
      <p>Their mission? To safeguard the integrity of the election by defending against potential trips, traps, and tribulations that might otherwise allow problems to arise.</p>
      <p>This is the story of how this diverse group adapted to a new landscape of cybercrime and nation-state attacks laid bare by the 2016 election.</p>
      <p>It’s the story of how they avoided unforced technical-errors and strengthened their processes against the backdrop of the Covid-19 pandemic that added new layers of pressure and complexity.</p>
      <p>It’s also the story of the depth, detail, and duration of the planning and preparation they undertook, which was fundamental to their ultimate success.</p>
    </section>
    <div className="scrollCtn">
      <a href="#NetworkTitle"><LottieAnim playAnim={true} looped={true} data={scroll}/></a>
    </div>
  </main>
)

export default Header
