import { useReducer,useEffect,useState,useCallback } from 'react'
import { Waypoint } from 'react-waypoint'
import Header from './components/Header/'
import Story from './components/Story/'
import Nav from './components/Nav/'
import Anims from './components/Anims/'
import Content from './components/Content/'
import Footer from './components/Footer/'
import Mobile from './components/Mobile/'
import StaticContent from './components/StaticContent/'
import DemocracyStateContext from './context/DemocracyStateContext'
import DemocracyDispatchContext from './context/DemocracyDispatchContext'
import democracyReducer, { initialDemocracyState } from './reducers/democracyReducer'
import './App.css'

const App =()=>{
  const [ state, dispatch ] = useReducer(democracyReducer, initialDemocracyState)
  const { direction,currentStory,mobile } = state
  const [y, setY] = useState(window.scrollY)

  const handleScroll = e=>{
    sendScrollPercent()
    handleNavigation(e)
  }


const handleNavigation = useCallback(
  e => {
    const window = e.currentTarget
    const up = y > window.scrollY
    if (up) {
      if(up!==direction)dispatch({type:'SET_DIRECTION',payload:true})
    } else {
      if(up!==direction)dispatch({type:'SET_DIRECTION',payload:false})
    }
    setY(window.scrollY)
  }, [y]
)


  const sendScrollPercent = ()=>{
    const doc = document.documentElement
    const winScroll = doc.scrollTop
    const winHeight = doc.scrollHeight - doc.clientHeight
    const scrolled = Math.round(winScroll / winHeight * 100)
    dispatch({type:'SET_SCROLL',payload:doc.scrollTop})
    dispatch({type:'SET_PERCENT',payload:scrolled})
  }


  useEffect(()=>{
    setY(window.scrollY)
    window.addEventListener('scroll',handleScroll)
    if(mobile!==window.innerWidth<=1024)dispatch({type:'SET_MOBILE',payload:window.innerWidth<=1024})
    return ()=> window.removeEventListener('scroll',handleScroll)
  },[handleNavigation])


  return(
    <DemocracyDispatchContext.Provider value={dispatch}>
      <DemocracyStateContext.Provider value={state}>
        <Mobile/>
        <div className="wrapper">
          <main className="Democracy">
            <div id="Intro"/>
            <Header/>
            <Waypoint onEnter={()=>{
                dispatch({type:'SET_STORY',payload:0})
                dispatch({type:'SET_SCALE_TRIANGLE',payload:false})}}>
              <div style={{"color":"#000"}}/>
            </Waypoint>
            <Story/>
          <Waypoint onEnter={()=>dispatch({type:'SET_STORY',payload:15})}>
            <div><StaticContent/></div>
          </Waypoint>
          <Footer/>
        </main>
          <Content/>
          <Nav/>
          <Anims/>
        </div>
    </DemocracyStateContext.Provider>
  </DemocracyDispatchContext.Provider>
  )
}
export default App

