import { useContext } from 'react'
import DemocracyStateContext from '../../context/DemocracyStateContext'
import DemocracyDispatchContext from '../../context/DemocracyDispatchContext'
import prev from '../../assets/icons/prev.svg'
import next from '../../assets/icons/next.svg'
import './Nav.css'
const Nav =()=>{
    const state = useContext(DemocracyStateContext)
    const dispatch = useContext(DemocracyDispatchContext)
    const { scenes,currentStory } = state
  return(
    <main className="Nav">
     <nav>
       {/*<ul> {scenes.map((scene,i)=><li
           className={i===currentStory?'selected':null}
           key={scene}><span><a href={`#${scene}`}>{scene}</a></span></li>)}</ul>*/}

        <ul>
          <li className={currentStory===0?'selected':null}><a href="#Intro">Intro</a></li>
          <li className={currentStory>=1 && currentStory<=5?'selected':null}><a href="#NetworkTitle">Network</a></li>
          <li className={currentStory>=6 && currentStory<=9?'selected':null}><a href="#History">History</a></li>
          <li className={currentStory>=10 && currentStory<=12?'selected':null}><a href="#Ethan_one">Dark web</a></li>
          <li className={13===currentStory?'selected':null}><a href="#Static">Static</a></li>
          <li className="btn">
            <a className={currentStory===0?'disabled':null} href={`#${scenes[currentStory-1]||''}`}><img width="18" src={prev} alt="prev"/></a>
            <a  className={currentStory===scenes.length-1?'disabled':null}  href={`#${scenes[currentStory+1]}`}><img width="18" src={next} alt="prev"/></a>
          </li>
        </ul>
     </nav>
   </main>
  )
}

export default Nav
